.deck {
    top: 50%;
    margin-top: -75px;
    left: 50%;
    margin-left: -53.625px;
    z-index: 998;
}

.hand1 {
    bottom: 0px;
    margin-bottom: -50px;
    left: 50%;
    margin-left: -53.625px;
    z-index: 998;
    transition-duration: .2s;
}


.hand2 {
    transform: rotate(90deg);
    transform-origin: 71.5% 54%;
    left: 0px;
    margin-left: -58px;
    top: 50%;
    margin-top: -53.625px;
    z-index: 998;
    transition-duration: .2s;
}

.hand3 {
    transform: rotate(180deg);
    top: 0px;
    margin-top: -50px;
    left: 50%;
    margin-left: -53.625px;
    z-index: 998;
    transition-duration: .2s;

}

.hand4 {
    transform: rotate(270deg);
    transform-origin: 28% 53%;
    right: 0px;
    margin-right: -58px;
    top: 50%;
    margin-top: -53.625px;
    z-index: 998;
    transition-duration: .2s;
}

.hand1.selected {
    margin-bottom: -25px;
    transition-duration: .5s;
}

